.cv{
    padding: 0 2rem 0 2rem;
}

.cv__item{
    text-align: left;
}
.cv__item a{
    text-decoration: none;
    color: #FFD369;
}
.cv__heading{
    text-align: left;
}
.cv__flex{
    display: flex;
}
.cv__heading:last-child{
    margin-left: 2rem;
}
@media screen and (max-width:1024px){
   .cv{
        font-size: 1.75rem;
   }
}