.item{
    display: flex;
    margin-bottom: 2rem;
    
  }
  .item__text{
    padding: 0 2rem 0 2rem;
    max-width: 750px;
  }
  .item__column, .item__column2{
    padding-left: 2rem;
  }
  .item__column2{
    max-width: 800px;
    margin: 0 auto;
  }
  .item__heading{
    margin-top: unset;
  }
  .item__column .item__heading{
    margin: 0 auto;
    
  }
  .item__image img{
    width: 16rem;
    height: 16rem;
  }
  .filters{
    display: flex;
    margin: 1rem auto;
    justify-content: space-around;
    max-width: 28rem;
    margin-bottom: 1rem;
    pointer-events: none;
  }
  .filters__button{
    border: none;
    border-radius: 1rem;
    font-size: 0.9rem;
    pointer-events: all;
  }
.item__button{
  margin: 0 auto;
}

  @media screen and (max-width:1024px){
    .filters{
      flex-wrap: wrap;
      justify-content: space-evenly;
    }
    .filters__button{
      margin-top: 1rem;
      margin-bottom: 1rem;
    }
    .item__text{
      font-size: 1.75rem;
    }
    .item{
      flex-direction: column-reverse;
      text-align: left;
      margin-top: 4rem;
    }
    .item__technologies{
      padding: 0 2rem 0 2rem;
    }
    .item__image{
      display: flex;
      justify-content: center;
    }
    .item__image img{
      width: 80vw;
      height: auto;
      margin-top: 2rem;
    }
    .item__heading{
      font-size: 3rem;
      padding: 0 2rem 0 2rem;
    }
    .item__button{
      width: 16rem;
      height: 4rem;
      order: 5;
    }
  }
  @media screen and (max-device-width:1024px) and (orientation:landscape) {
    .item{
      padding: 0 2rem 0 2rem;
    }
    .filters__button{
      width: 6rem;
      height: 2.5rem;
    }
    .item{
      flex-direction: row;
    }
    .item__image img{
      width: 24rem;
      height: 24rem;
    }
  
  }