.App {
  text-align: center;
  height: 100vh;
  background-color: #222831;
  color: #fff;
  display: flex;
  align-items: center;
}
html{
  font-size: 16px;
}
body{
  font-family: 'Work Sans', sans-serif;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}
@media screen and (max-width:1024px){

}

.wrapper{
  margin-left: auto;
  margin-right: auto;
}

.App-header {
  width: 40rem;
  height: 40rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
  align-self: center;
}
.App-container{
  width: 85vw;
  justify-content: center;
  display: flex;
  
  padding-left: 2rem;
  padding-right: 2rem;
  margin: 0 auto;

}
.App-main{
  width: 70rem;
  height: 40rem;
  position: relative;
  z-index: 2;
}

@media screen and (max-width:1024px){
  .App-container{
    flex-direction: column;
    width: 100vw;
    padding: 0 0 0 0;
    
  }
 
  .App-main{
    width: 100%;
    flex-grow: 1;
  }
  .imgContainer{
    margin: unset;
  }
  .App-icon-container{
    display: none;
  }

  .content{
    border-radius: unset;
  }
  html{
    font-size: 10px;
  }
  .wrapper{
    width: 100vw;
    height: 100%;
    margin: unset;
    display: flex;
    margin-bottom: auto;   
  }
  
  .filters{
    max-width: 50%;
  }
}
@media screen and (max-device-width:1024px) and (orientation:landscape) {
  .App{
    
    min-height: -webkit-fill-available;
  }  
}
@media screen and (max-width:704px){
  .filters{
    max-width: 100%;
  }
  .filters__button{
    height: 2rem;
    
  }
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.button{
  color: #393E46;
  background-color: #FFD369;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 1rem;
  border: none;
  border-radius: 1rem;
  height: 2rem;
  width: 10rem;
  text-decoration: none;
}
.button:hover,
.button:focus{
  box-shadow: 0 0 10px 0 #FFD369  inset, 0 0 10px 4px #FFD369;
  background-color: transparent;
  color: #fff;
}