.navigation{
    display: flex;
    height: 40rem;
    flex-direction: column;
    margin-right: 2rem;
    border: 1px solid #393E46;
    border-radius: 20px;
    background-color: #393E46;
    max-width: 12rem;
    position: relative;
    z-index: 2;
    padding: 0 1rem 0 1rem;
  }
  
  .imgContainer{
    width: 8rem;
    height: 8rem;
    margin: 2rem auto;
  }
  .imgContainer img{
    width: 100%;
    height: 100%;
    border-radius: 20px;
  }
  .navigation__link{
    text-decoration: none;
    color: #EEEEEE;
    position: relative;
    cursor: pointer;
    line-height: 1;
    width: 80%;
    margin: 1rem auto;
    font-size: 1.25rem;
  }
  
  .navigation__link:after{
    display: block;
    position: absolute;
    left: 0;
    width: 0;
    height: 2px;
    background-color:#FFD369;
    content: '';
    transition: width 0.3s ease-out;
  }
  .navigation__link:hover:after,
  .navigation__link:focus:after{
    width: 100%;
  
  }
  .navigation__link:hover{
    color: #FFD369;
  }
  
  
  
.navigation__button{
    margin: 2rem auto; 
    margin-bottom: 2rem;
  }
  .navigation__button:hover{
    color: #EEEEEE;
  }


  .App-icon-container{
    margin-top: auto;
  }
  
 

  @media screen and (max-width:1024px){
    .navigation{
        max-width: unset;
        height: 82px;
        flex-direction: row;
        align-items: center;
        border-radius: unset;
        margin-right: unset;
        border-bottom: 1px solid #FFD369; 
      }
      .navigation__link{
        width: 6rem;
      }
      .navigation__button{
        width: 8rem;
      }
      .imgContainer{
        display: none;
      }
      .navigation__link{
        padding: 15px;
      }
  }