.blue{
    background-color: #61dafb ;

    box-shadow: 0 0 40px 40px #61dafb  inset, 0 0 0 0 #61dafb ;
    -webkit-transition: all 150ms ease-in-out;
    transition: all 150ms ease-in-out;
}
.red{
    background-color: #d0021b ;

    box-shadow: 0 0 40px 40px #d0021b   inset, 0 0 0 0 #d0021b  ;
    -webkit-transition: all 150ms ease-in-out;
    transition: all 150ms ease-in-out;
}
.linen{
    background-color: #faf0e6 ;

    box-shadow: 0 0 40px 40px #faf0e6  inset, 0 0 0 0 #faf0e6 ;
