    -webkit-transition: all 150ms ease-in-out;
    transition: all 150ms ease-in-out;
}

.blue:hover,
.blue:focus{
    box-shadow: 0 0 10px 0 #61dafb  inset, 0 0 10px 4px #61dafb ;
    background-color: transparent;
    color: white;
}

.red:hover,
.red:focus{
    box-shadow: 0 0 10px 0 #d0021b  inset, 0 0 10px 4px #d0021b;
    background-color: transparent;
