body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overflow: hidden;


}


code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

@media screen and (max-width:1024px){
  .item{
    margin-bottom: 4rem;
  }
}

.triangle{
  width: 0;
  height: 0;
  border: 0 solid transparent;
  border-right-width: 13px;
  border-left-width: 12px;
  border-bottom: 22px solid #FFD369;
  top: 5rem;
  position: absolute;
  z-index: 1;
  left: 2rem;
  display: none;
}
.triangleReversed{
  width: 0;
  height: 0;
  border: 0 solid transparent;
  border-left-width: 13px;
  border-right-width: 12px;
  border-top: 22px solid #FFD369;
  bottom: 1rem;
  position: absolute;
  z-index: 1;
  left: 20rem;
  display: none;
}
.triangleRotated{
  left: unset;
  right: 19rem;
  top: 6rem;
  transform: rotate(180);
  display: none;
}

.innerTriangle{
  width: 0;
  left: -7px;
  top: 5px;
  height: 0;
  border: 0 solid transparent;
  border-right-width: 8px;
  border-left-width: 7px;
  border-bottom: 15px solid #222831;
  position: absolute;
  z-index: 2;
  display: none;
}
.innerTriangleReversed{
  width: 0;
  bottom: 5px;
  right: -7px;
  height: 0;
  border: 0 solid transparent;
  border-left-width: 8px;
  border-right-width: 7px;
  border-top: 15px solid #222831;
  position: absolute;
  z-index: 2; 
}



.fade-enter {
  opacity: 0;
  transform: translate(0, 25px);
  z-index: 1;
}
.fade-enter.fade-enter-active {
  opacity: 1;
  transform: translate(0, 0);

  transition: opacity 250ms ease-out, transform 300ms ease;
}
.fade-exit {
  opacity: 1;
  transform: translate(0, 0);
}
.fade-exit.fade-exit-active {
  opacity: 0;
  transform: translate(0, 30px);

  transition: opacity 250ms ease-out, transform 300ms ease;
}

.content{
  position: absolute;
  background-color: #393E46;
  border-radius: 20px;
  align-self: center;
  width: 100%;
  height: 100%;
  overflow-y: auto;
  overflow-x: hidden;
}

.animation{
    border-color: #FFD369;

    box-shadow: 0 0 40px 40px #FFD369 inset, 0 0 0 0 #FFD369;
    -webkit-transition: all 150ms ease-in-out;
    transition: all 150ms ease-in-out;

}

.animation:hover {
  box-shadow: 0 0 10px 0 #FFD369 inset, 0 0 10px 4px #FFD369;
}