.bg-image{
    background-image: url('../../assets/background.jpg');
    background-size: cover;
    opacity: 0.1;
    width: 100%;
    height: 100%;
    position: absolute;
    pointer-events: none;
  }
  .main{
    height: 100%;
    width: 100%;
    display: flex;
    flex-direction: column;
  }

.main__list{
    text-align: left;
    max-width: 600px;
    margin: auto auto auto auto;
    font-size: 1.50rem;
}
.main__item{
    list-style-type: none;
}
.main__item:not(:first-child){
    margin-top: 2rem;
}
.main__item a{
    color: #fff;
    text-decoration: none;
   
}
.main__item a:hover{
    color: #FFD369;
    
}
.main__heading{
    font-size: 2rem;
    text-align: left;
    max-width: 250px;
    margin: 0 auto;
  }

@media screen and (max-width:1024px){
    .main{
        font-size: 1.75rem;
    }
    .main__list{
        margin: 4rem auto auto auto; 
    }
    .main__text{
        padding: 0 1rem 0 1rem;
    }
}
@media screen and (max-device-width:1024px) and (orientation:landscape) {
    .bg-image{
        height: 200%;
    }
    .main__text{
        padding: 0 4rem 0 4rem;
    }
}